body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
}

#joystick-circle {
  position: absolute;
  background: #f00;
  width: 75px;
  height: 75px;
  border-radius: 50%;
  bottom:20px;
  left:20px;
}

#aim-circle {
  position: absolute;
  background: #f00;
  width: 75px;
  height: 75px;
  border-radius: 50%;
  bottom:20px;
  right:20px;
}
